@import-normalize;
@import url("https://fonts.googleapis.com/css2?family=Marcellus+SC&family=Mulish:ital,wght@0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;0,1000;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900;1,1000&family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&family=Source+Sans+Pro:ital,wght@0,200;0,300;0,400;0,600;0,700;0,900;1,200;1,300;1,400;1,600;1,700;1,900&family=Tenor+Sans&display=swap");

:root {
    --background-primary: #121212;
    --primary: #e4e4e4;
    --secondary: #242424;
    --third: #a6a6a6;
    --fourth: #ffffff;
    --fifth: #00000033;
    --font-1: "Source Sans Pro", sans-serif;
    --font-2: "Marcellus SC", serif;
    --font-3: "Mulish", sans-serif;
    --font-4: "Roboto", sans-serif;
    --border: 1px dashed #343434;
    --border-preview: 1px dashed #343434;
    --preview-color-1: #151515;
    --preview-color-2: #1b1d20;
    --preview-color-3: #19191a;
    --preview-color-4: #e4fefe;
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-size: 0.85rem;
}

ul,
li {
    list-style: none;
}

a {
    color: inherit;
    text-decoration: none;
}

body {
    min-height: 100vh;
    background-color: var(--background-primary);
    color: var(--fourth);
    font-family: var(--font-1);
    position: relative;
}

.btn {
    background: none;
    border: var(--border);
    padding: 10px;
    cursor: pointer;
    transition: all 0.1s ease-in-out;
    color: var(--third);
    background-color: var(--background-primary);
}

.btn:hover {
    scale: 1.025;
    color: var(--primary);
}

.container {
    display: flex;
    justify-content: space-between;
}

.form {
    width: 50%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: 40px 0;
}

.preview {
    width: 50%;
    position: relative;
    margin: 40px 0;
}

.preview .fixed {
    position: fixed;
    border: var(--border-preview);
    width: 593px;
    aspect-ratio: 0.7;
    display: flex;
    user-select: none;
}

.preview-experience-position {
    font-weight: bold;
    padding-bottom: 3px;
}

.preview-education-position {
    font-weight: bold;
    padding-bottom: 3px;
}

.preview-education-degree {
    font-weight: 200;
}

.left-preview {
    flex: 1;
    height: 100%;
    background-color: var(--preview-color-1);
    display: flex;
    flex-direction: column;
}

.right-preview {
    flex: 2.5;
    height: 100%;
    background-color: var(--preview-color-2);
    font-family: var(--font-3);
}

.preview-photo {
    width: 80%;
    aspect-ratio: 1;
    margin-top: 20px;
    align-self: center;
}

.form-photo {
    align-self: flex-end;
}

.preview-photo img {
    width: 100%;
    height: 100%;
    aspect-ratio: 1;
    border-radius: 50%;
    box-shadow: 0px 0px 10px 0px black;
}

.left-preview h1 {
    margin: 10px 0 10px 0;
    margin-left: -20px;
    padding: 5px 0 4px 20px;
    background-color: var(--preview-color-2);
    font-family: var(--font-4);
    font-weight: 500;
    letter-spacing: 1px;
}

.left-preview li {
    margin: 7px 0 7px 0;
}

.left-preview span {
    font-family: var(--font-4);
    color: var(--preview-color-4);
    font-weight: 500;
}

.preview-general {
    padding-left: 20px;
}

.preview-contact {
    padding-left: 20px;
}

.preview-hobbies li {
    margin: 0;
    margin-bottom: 3px;
}

.preview-hobbies {
    padding-left: 20px;
}

ol li {
    list-style: disc;
    list-style-position: inside;
}

.btn.pdf {
    position: absolute;
    bottom: -35px;
    left: 60%;
    transform: translateX(-50%);
    height: 30px;
    padding: 0 10px;
    width: 100px;
}

.btn.autofill {
    position: absolute;
    bottom: -35px;
    left: 40%;
    transform: translateX(-50%);
    height: 30px;
    padding: 0 10px;
    width: 100px;
}

.btn.language {
    position: fixed;
    border: none;
    top: 0;
    right: 25px;
    height: 30px;
    padding: 0 10px;
}

.btn.theme {
    position: fixed;
    border: none;
    top: 0;
    right: 100px;
    height: 30px;
    padding: 0 10px;
}

.active {
    text-decoration: underline;
    font-weight: bold;
    font-size: 1rem;
}

.btn.language:hover {
    color: var(--primary);
    scale: none;
}

.preview-header {
    background-color: var(--preview-color-3);
    padding: 40px 20px;
    display: flex;
    justify-content: center;
    flex-direction: column;
}

.header-name {
    font-size: 2rem;
    letter-spacing: 3px;
    font-family: var(--font-2);
}

.header-title {
    text-align: right;
    font-weight: 100;
    font-size: 1rem;
}

.preview-description {
    text-align: justify;
    padding-top: 20px;
}

.preview-main h1 {
    letter-spacing: 10px;
    padding: 5px 0;
    border-bottom: var(--border-preview);
    color: var(--preview-color-4);
}

.preview-experience-1 {
    padding-top: 10px;
}

.preview-experience-2 {
    padding-top: 10px;
}

.preview-education-1 {
    padding-top: 10px;
}

.preview-education-2 {
    padding-top: 10px;
}

.preview-skills ol {
    padding-top: 10px;
}

.preview-skills li {
    margin-bottom: 3px;
}

.preview-main {
    padding: 20px 20px 0 20px;
    display: flex;
    flex-direction: column;
    height: 70%;
    justify-content: space-between;
}

.preview-experience-main {
    display: flex;
    justify-content: space-between;
}

.preview-education-main {
    display: flex;
    justify-content: space-between;
}

.form input {
    background-color: var(--secondary);
    color: var(--third);
    border: none;
    padding: 7px;
    margin: 7px;
    display: flex;
    width: clamp(300px, 35vw, 585px);
    transition: all 0.25s ease-in-out;
}

.form input:hover {
    letter-spacing: 2px;
    font-weight: bold;
    background-color: var(--primary);
    color: var(--background-primary);
}

.flex-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 7px;
    margin: 7px;
    border: var(--border);
    background-color: var(--fifth);
}

.flex-container:first-child {
    margin-top: 0;
}

.flex-container:last-child {
    margin-bottom: 0;
}

.form h1 {
    padding: 10px;
    letter-spacing: 3px;
    text-align: right;
    font-family: var(--font-3);
    color: var(--third);
}

.footer {
    position: absolute;
    bottom: 5px;
    left: 25%;
    transform: translateX(-50%);
}

.about {
    display: flex;
    align-items: center;
    color: var(--primary);
}

.github-icon {
    height: 25px;
    padding-left: 10px;
    padding: 0 10px;
    transition: transform 1s ease-in-out;
}

.github-icon:hover {
    transform: rotate(360deg);
}

.footer a {
    color: var(--third);
}

.footer a:hover {
    color: var(--primary);
}
